.home-section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0f0f0f;
  padding: 0 5%;
  color: white;
}

.home-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  gap: 4rem;
}

.home-image img {
  width: 500px;
  height: 500px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid cyan;
  box-shadow: 0 0 20px cyan;
}

.home-content {
  max-width: 600px;
}

.home-content h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.highlight {
  color: cyan;
  font-weight: bold;
}

.home-content p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 2rem;
  color: #ccc;
}

.home-contact-btn {
  background-color: cyan;
  padding: 0.7rem 1.5rem;
  border-radius: 999px;
  color: #000;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.3s ease;
}

.home-contact-btn:hover {
  background-color: #00cccc;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .home-section {
    height: auto;
    padding: 100px 3% 0; /* ADD top padding to push down from navbar */
    text-align: center;
  }


  .home-container {
    flex-direction: column;
    gap: 2rem;
  }

  .home-image img {
    width: 300px;
    height: 300px;
  }

  .home-content {
    max-width: 100%;
    text-align: center;
  }

  .home-content h1 {
    font-size: 2rem;
  }

  .home-content p {
    font-size: 1rem;
  }

  .home-contact-btn {
    padding: 0.6rem 1.2rem;
  }
}
